import React, { useEffect, useState, useRef } from "react";



import { getStore, gen16CodeId, ObjectKeys, callDispatchEvent } from "../../../stores/Utils";



import "./dashboard.css"
import { UpdAccount_Cmpt } from "..";
import { MarriageCertification_Cmpt } from "../../MarriageCertificate";
import { BirthCertification_Cmpt } from "../../BirthCerticate";

const UpdAccount = UpdAccount_Cmpt();

const MarriageCertification = MarriageCertification_Cmpt();
const BirthCertification = BirthCertification_Cmpt();





const frmNm = "upd_account"



const ViewAccount  = ({ data, elmId, updObs, close }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	const [updView, set_updView] = useState(false);
	const [mctView, set_mctView] = useState(false);
	const [bctView, set_bctView] = useState(false);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			set_obs(gen16CodeId())
		}
	});


	const openBCT = ()=>{
		closeViews()
		set_bctView(true);
	}

	


	const openMCT = ()=>{
		closeViews()
		set_mctView(true);
	}

	const closeViews = ()=>{
		set_updView(false);
		set_bctView(false)
		set_mctView(false);
	}

	

	if(updView){
		return <UpdAccount close={()=>set_updView(false)}/>
	}

	if(mctView){
		return <MarriageCertification close={()=>set_mctView(false)}/>
	}
	
	if(bctView){
		return <BirthCertification close={()=>set_bctView(false)}/>
	}

	return 	<DetailAccount 
				close={close} 
				openUpd={set_updView} 
				openBCT={openBCT}
				openMCT={openMCT}
			/>

}






