export default ViewAccount;


const compareJSON = (fls,obj, bkUp) =>{
	let r = 0;
	let data = {}
	for(let i in fls){
		let k1 = fls[i];
		if(obj[k1] ){
			if(typeof obj[k1] === "object"){
				ObjectKeys(obj[k1]).map(fk2=>{
					if(obj?.[k1]?.[fk2]!==bkUp?.[k1]?.[fk2]){
						r = 1;
						data[k1] = obj[k1];
					}
				})
			}
			else if(obj[k1] &&  obj[k1]!==bkUp[k1]){
				r = 1;
				data[k1] = obj[k1];
			}
		}
	}
	return {data, hasChange:r};
  }




  

const TextView  = ({ init, label, width }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			set_obs(gen16CodeId())
		}
	});

	if(!init){
		return null
	}

	return (
		<div>
			<p className="label">
				{label}
			</p>
			<div  
				style={width? {width: width}: null}
				className="r7gAOb yyJm8b"
			>
				{init}
			</div>
		</div>
	)
}